/* Default styles */
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
  background-color: blue;
}

h1 {
  color: white;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  margin: 10px;
  border-color: white;
  transition: background-color 0.3s ease;
}

/* Button colors */
.blue {
  background-color: blue;
  color: white;
}

.green {
  background-color: green;
  color: white;
}

.yellow {
  background-color: rgb(184, 184, 18);
  color: rgb(248, 236, 236);
}

.red {
  background-color: red;
  color: white;
}

/* Media queries for mobile responsiveness */
@media (max-width: 768px) {
  .App {
    padding: 20px; /* Add padding to center content */
  }

  h1{
    text-align: center;
  }
  button {
    width: 100%; /* Make buttons full width */
  }
}
